import React from "react";
import { Badge, Container, Row, Col } from "reactstrap";
import { connect } from "react-redux";
import ClipLoader from "react-spinners/ClipLoader";

import { setMovies, setLoading } from "../../actions/index.js";
import Pagination from "../Pagination/Pagination.jsx";
import Card from "../Card/Card.jsx";
import Search from "../Search/Search.jsx";
import { fetchMovies, updateUrlQueries, override } from "../../helpers/helpers.js";

import "./Movies.css";

const MoviesComponent = (props) => {
	const { movies, loadingApp, filteredMovies, setMovies, setLoading } = props;
	const { itemToDisplay } = props;

	React.useEffect(() => {
		setLoading(true);

		const { page } = updateUrlQueries(props, {
			page: 1,
			"filtered-by": "",
			"to-display": 12,
		});

		fetchMovies(page, (data) => setMovies(data, page));
	}, []);

	if (loadingApp) return <ClipLoader css={override} size={150} loading={loadingApp} />;

	const data = filteredMovies.length > 0 ? filteredMovies : movies;

	return (
		<div data-testid="movies-container">
			<Container className="movies-container">
				<Pagination {...props} />
				{data.length > 0 ? (
					<>
						<Search />
						<hr />
						<Row className="movies-badges">
							<Col>
								<Badge color="danger">
									Bordure rouge = Films que vous aimez pas
								</Badge>
							</Col>
							<Col>
								<Badge color="success">Bordure verte = Films que vous aimez</Badge>
							</Col>
						</Row>
						<hr />
						<div data-testid="cards-container">
							<Row>
								{data.map((movie, i) => {
									if (i + 1 <= itemToDisplay) {
										return (
											<Col md="4" key={i}>
												<Card {...props} movie={movie} />
											</Col>
										);
									}
									return <React.Fragment key={i} />;
								})}
							</Row>
						</div>
					</>
				) : (
					<div data-testid="nothing-to-display" className="nothing-to-display">
						Aucun contenu à afficher...
					</div>
				)}
				<Pagination {...props} />
			</Container>
		</div>
	);
};

const mapStateToProps = (state) => ({
	pagination: state.pagination,
	loadingApp: state.loadingApp,
	movies: state.movies,
	filteredMovies: state.filteredMovies,
	categories: state.categories,
	filteredBy: state.filteredBy,
	itemToDisplay: state.itemToDisplay,
});
const mapDispatchToProps = {
	setMovies,
	setLoading,
};

export default connect(mapStateToProps, mapDispatchToProps)(MoviesComponent);
