import React from "react";
import {
	Dropdown,
	DropdownToggle,
	DropdownMenu,
	DropdownItem,
	Row,
	Col,
	Pagination,
	PaginationItem,
	PaginationLink,
	Alert,
} from "reactstrap";
import Swal from "sweetalert";
import { connect } from "react-redux";

import { paginate, setItemToDisplay, filterByCategories } from "../../actions/index.js";
import {
	categoriesNames,
	dropDownModifier,
	getPagingRange,
	updateUrlQueries,
	noOdds,
} from "../../helpers/helpers.js";

import "./Pagination.css";

class PaginationComponent extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			dropdownOpen1: false,
			dropdownOpen2: false,
		};
	}

	setDropdownOpen1 = () => this.setState({ dropdownOpen1: !this.state.dropdownOpen1 });

	setDropdownOpen2 = () => this.setState({ dropdownOpen2: !this.state.dropdownOpen2 });

	swal = (text) => Swal({ title: "Oops!", text, icon: "info" });

	handlePaginate = (action) => {
		const { paginate, pagination, filteredBy } = this.props;
		const { page, total_pages } = pagination;

		if (filteredBy.length > 0)
			return this.swal("Veuillez desactiver le filtre avant de continuer");

		const next = action === "next";

		if (typeof action === "string") {
			if (next && page > total_pages) return; // Unable to paginate to paginate (next)
			if (!next && page <= 0) return; // Unable to return (prev)
			if (next) {
				pagination.page++;
			} else {
				pagination.page--;
			}
		} else {
			pagination.page = action;
		}

		updateUrlQueries(this.props, { page: pagination.page });
		paginate(this.props);
	};

	handleItemToDisplay = (nbr) => {
		const { setItemToDisplay } = this.props;

		if (this.disabled())
			return this.swal(
				"La recherche est active, veuillez videz le champs de recherche avant de continuer"
			);
		updateUrlQueries(this.props, { "to-display": nbr });
		setItemToDisplay(nbr);
	};

	disabled = () => {
		const { movies, searchValue } = this.props;
		const disabled = movies.length <= 4 || searchValue !== "";

		return disabled;
	};

	handleFilterByCategories = (categorie) => {
		const { filterByCategories, itemToDisplay } = this.props;

		if (this.disabled())
			return this.swal(
				"La recherche est active, veuillez videz le champs de recherche avant de continuer"
			);
		filterByCategories(this.props, categorie);
		updateUrlQueries(this.props, {
			"filtered-by": categorie === "reset" ? "" : categorie,
			"to-display": categorie !== "reset" ? 0 : itemToDisplay,
		});
	};

	dislayCategories = () => {
		const { categories } = this.props;

		return categoriesNames(categories).map((c, i) => (
			<DropdownItem onClick={() => this.handleFilterByCategories(c)} key={i}>
				{c}
			</DropdownItem>
		));
	};

	render = () => {
		const { pagination, movies, filteredBy, itemToDisplay } = this.props;
		const { page, total_pages } = pagination;
		const prevDisabled = page <= 1;
		const nextDisabled = page >= total_pages;
		const paginateRange = getPagingRange(page, { total: total_pages, length: 4 });
		const toDisplayRange2 = getPagingRange(1, { total: movies.length, length: movies.length });

		return (
			<div data-testid="pagination">
				<Row className="movies-pagination">
					{movies.length > 0 && (
						<>
							{filteredBy === "" && (
								<>
									<Col>
										<Pagination aria-label="pagination">
											<PaginationItem
												disabled={prevDisabled || this.disabled()}
											>
												<PaginationLink
													disabled={prevDisabled || this.disabled()}
													onClick={() => this.handlePaginate("prev")}
													previous
												/>
											</PaginationItem>

											{paginateRange.map((value, i) => {
												const disabled = value == page;

												return (
													<PaginationItem
														key={i}
														disabled={disabled || this.disabled()}
													>
														<PaginationLink
															onClick={() =>
																this.handlePaginate(value)
															}
															disabled={disabled || this.disabled()}
														>
															{value}
														</PaginationLink>
													</PaginationItem>
												);
											})}

											<PaginationItem
												disabled={nextDisabled || this.disabled()}
											>
												<PaginationLink
													disabled={nextDisabled || this.disabled()}
													onClick={() => this.handlePaginate("next")}
													next
												/>
											</PaginationItem>
										</Pagination>
									</Col>
									<Col>
										<Dropdown
											isOpen={this.state.dropdownOpen1}
											toggle={this.setDropdownOpen1}
										>
											<DropdownToggle color="primary" caret>
												NOMBRE DE CARTES À AFFICHER
											</DropdownToggle>
											<DropdownMenu modifiers={dropDownModifier}>
												{noOdds(
													toDisplayRange2,
													movies.length <= 6 ? 1 : 3
												).map((nbr, i) => {
													return (
														<div
															key={i}
															onClick={() =>
																this.handleItemToDisplay(nbr)
															}
															data-testid={`to-display-${nbr}`}
														>
															<DropdownItem tabIndex={i}>
																{nbr}
															</DropdownItem>
														</div>
													);
												})}
											</DropdownMenu>
											{!this.disabled() && (
												<Alert color="light" className="text-center">
													{itemToDisplay}{" "}
													{itemToDisplay > 1
														? "cartes affichées"
														: "carte affichée"}
												</Alert>
											)}
										</Dropdown>
									</Col>
								</>
							)}
						</>
					)}
					<Col>
						<Dropdown isOpen={this.state.dropdownOpen2} toggle={this.setDropdownOpen2}>
							<DropdownToggle color="primary" caret>
								CATÉGORIES {filteredBy && <> ({filteredBy.toUpperCase()}) </>}
							</DropdownToggle>
							<DropdownMenu modifiers={dropDownModifier}>
								{this.dislayCategories()}
							</DropdownMenu>
							<Alert color="light" className="text-center">
								{filteredBy !== "" && (
									<b
										onClick={() => this.handleFilterByCategories("reset")}
										className="reset-filter"
									>
										REINITIALISER
									</b>
								)}
							</Alert>
						</Dropdown>
					</Col>
				</Row>
			</div>
		);
	};
}

const mapStateToProps = (state) => ({
	pagination: state.pagination,
	categories: state.categories,
	movies: state.movies,
	keepMovies: state.keepMovies,
	filteredBy: state.filteredBy,
	itemToDisplay: state.itemToDisplay,
	searchValue: state.searchValue,
});
const mapDispatchToProps = { paginate, setItemToDisplay, filterByCategories };

export default connect(mapStateToProps, mapDispatchToProps)(PaginationComponent);
