import React from "react";
import { Switch, NavLink, Route } from "react-router-dom";
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem } from "reactstrap";

import Movies from "../Movies/Movies.jsx";
import DetailMovieComponent from "../Movies/DetailMovie.jsx";
import Historical from "../Historical/Historical.jsx";
import NoMatch from "../NoMatch/NoMatch.jsx";
import Home from "../Home/Home.jsx";
import UseCase from "../UseCase/UseCase.jsx";

import { path } from "../../helpers/helpers";

import "./App.css";

const App = () => {
	const [isOpen, setIsOpen] = React.useState(false);
	const toggle = () => setIsOpen(!isOpen);

	return (
		<>
			<Navbar color="dark" dark expand="md">
				<NavbarBrand href={path}>ACCUEIL</NavbarBrand>
				<NavbarToggler onClick={toggle} />
				<Collapse isOpen={isOpen} navbar>
					<Nav data-testid="navbar" className="mr-auto" navbar>
						<NavItem>
							<NavLink
								className="nav-link"
								to={`/movies`}
								activeClassName="selected"
								data-testid="movies-nav-link"
							>
								<i className="fa fa-film"></i> &nbsp; Films
							</NavLink>
						</NavItem>
						<NavItem>
							<NavLink
								className="nav-link"
								to={`/historical`}
								activeClassName="selected"
							>
								<i className="fa fa-history"></i> &nbsp; Mon historique
							</NavLink>
						</NavItem>
						<NavItem>
							<NavLink
								className="nav-link"
								to={`/use-case`}
								activeClassName="selected"
							>
								<i className="fa fa-user"></i> &nbsp; Cas d'utilisation
							</NavLink>
						</NavItem>
					</Nav>
				</Collapse>
			</Navbar>

			<Switch>
				<Route path={"/"} exact component={Home} />
				<Route path={`/use-case`} component={UseCase} />
				<Route path={`/historical`} component={(props) => <Historical {...props} />} />
				<Route exact path={`/movies`} component={(props) => <Movies {...props} />} />
				<Route
					path={`/movies/:id`}
					component={(props) => <DetailMovieComponent {...props} />}
				/>
				<Route component={NoMatch} />
			</Switch>
		</>
	);
};

export default App;
