import React from "react";
import { TabContent, TabPane, Nav, NavItem, NavLink, Container } from "reactstrap";
import qs from "qs";

import { updateUrlQueries } from "../../helpers/helpers.js";

import LikedUnlikedMoviesComponent from "./components/LikedUnlikedMovies.jsx";
import Trash from "./components/Trash.jsx";

import "./Historical.css";

const HistoricalComponent = (props) => {
	const [activeTab, setActiveTab] = React.useState("");
	const toggle = (tab) => {
		if (activeTab !== tab) {
			setActiveTab(tab);
			updateUrlQueries(props, { tab });
		}
	};
	const _props = {
		location: props.location,
		history: props.history,
	};

	React.useEffect(() => {
		const {
			location: { search },
		} = props;
		const queryParams = qs.parse(search, { ignoreQueryPrefix: true });
		const { tab } = updateUrlQueries(props, {
			tab: queryParams["tab"] || 1,
		});

		toggle(tab);
	}, [activeTab]);

	return (
		<Container id="historical">
			<Nav tabs>
				<NavItem>
					<NavLink
						className={`${activeTab === "1" ? "active" : ""}`}
						onClick={() => toggle("1")}
					>
						<i className="fa fa-thumbs-up"></i> &nbsp; Films aimés
					</NavLink>
				</NavItem>
				<NavItem>
					<NavLink
						className={`${activeTab === "2" ? "active" : ""}`}
						onClick={() => toggle("2")}
					>
						<i className="fa fa-thumbs-down"></i> &nbsp; Films non aimés
					</NavLink>
				</NavItem>
				<NavItem>
					<NavLink
						className={`${activeTab === "3" ? "active" : ""}`}
						onClick={() => toggle("3")}
					>
						<i className="fa fa-trash"></i> &nbsp; Corbeille
					</NavLink>
				</NavItem>
			</Nav>
			<TabContent activeTab={activeTab}>
				<TabPane tabId="1">
					{activeTab === "1" && (
						<LikedUnlikedMoviesComponent {..._props} componentName="likedMovies" />
					)}
				</TabPane>
				<TabPane tabId="2">
					{activeTab === "2" && (
						<LikedUnlikedMoviesComponent {..._props} componentName="unlikedMovies" />
					)}
				</TabPane>
				<TabPane tabId="3">{activeTab === "3" && <Trash {..._props} />}</TabPane>
			</TabContent>
		</Container>
	);
};

export default HistoricalComponent;
